.burger-menu, .burger-menu.open {
  display: inline-block;
  cursor: pointer;
  position: fixed;
  right: 20px;
  top: 30px;
  z-index: 9999;
  background: #292929;
  padding: 10px;
  border-radius: 25px;
  box-shadow: 0 1px 4px crimson;
}

.burger-menu .bar1, .bar2, .bar3 {
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  margin: 4px 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.burger-menu.open .bar1 {
  -webkit-transform: rotate(-765deg) translate(-4px, 4px);
          transform: rotate(-765deg) translate(-4px, 4px);
  -webkit-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
          transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.burger-menu.open .bar2 {
  opacity: 0;
}

.burger-menu.open .bar3 {
  -webkit-transform: rotate(765deg) translate(-6px, -6px);
          transform: rotate(765deg) translate(-6px, -6px);
  -webkit-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
          transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

